@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
    font-family: 'Roboto', sans-serif;
}

.App {
    width: 550px;
    margin: auto;
    margin-top: 50px;
}
.Header * {
    display: inline;
}

.Header {
    margin-bottom: 10px;
}

.Header h1 {
    font-size: 60px;
    margin-right: 75px;
}

.Header div {
    margin: 10px;
    color: rgb(90, 90, 90);
    font-size: 25px;
}
.Search {
    display: inline;
    padding: 12px;
    border: 1px solid rgb(90, 90, 90);
    border-radius: 3px;
    background-color: #fff;
    font-size: 20px;
    outline: none;
    margin-right: 3px;
    width: 273px;
}
.Filter * {
    display: inline;
    font-size: 20px;
    padding: 12px 20px;
    border: 1px solid rgb(90, 90, 90);
}

.Filter {
    display: inline;
    position: relative;
}

.Filter :nth-child(1) {
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
}

.Filter :nth-child(2) {
    border-left: none;
    border-right: none;
}

.Filter :nth-child(3) {
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
}


.Filter :nth-child(1), .Filter :nth-child(2), .Filter :nth-child(3) {
    transition: all 0.4s ease;
}

.Filter :nth-child(1):hover, .Filter :nth-child(2):hover, .Filter :nth-child(3):hover {
    background-color: rgb(90, 90, 90);
    color: white;
    cursor: pointer;
}


#active {
    background-color: steelblue;
    color: white;
    border-color: steelblue;
}

#active:hover {
    cursor: default;
    background-color: steelblue;
    color: white;
    border-color: steelblue;
}
.List {
    list-style: none;
    width: 100%;
    font-size: 20px;
}

.List li {
    position: relative;
    right: 40px;
    padding: 20px;
    border: 1px solid rgb(90, 90, 90);
    border-bottom: none;
}

.List li * {
    display: inline-block;
}

.List li:last-child {
    border-bottom: 1px solid rgb(90, 90, 90);
}
.Item div:nth-child(1):hover {
    cursor: pointer;
}

.Item div:nth-child(2) {
    position: absolute;
    right: 65px;
    bottom: 12px;
    font-size: 15px;
    font-weight: 700;

    color: tomato;
    border: 1px solid tomato;
    padding: 10px;
    border-radius: 3px;

    transition: all 0.4s ease;
}

.Item div:nth-child(3) {
    position: absolute;
    right: 10px;
    bottom: 12px;
    font-size: 15px;
    font-weight: 700;

    color: steelblue;
    border: 1px solid steelblue;
    padding: 10px 20px;
    border-radius: 3px;

    transition: all 0.4s ease;
}


.Item div:nth-child(2):hover {
    cursor: pointer;
    background-color: tomato;
    color: white;
}

.Item div:nth-child(3):hover {
    cursor: pointer;
    background-color: steelblue;
    color: white;
}


.done {
    text-decoration: line-through;
}

.important {
    font-weight: 700;
    color: steelblue;
}
.Submit input:nth-child(1) {
    padding: 12px;
    border: 1px solid rgb(90, 90, 90);
    border-radius: 3px;
    background-color: #fff;
    font-size: 20px;
    outline: none;
    margin-right: 3px;
    width: 410px;
}

.Submit input:nth-child(2) {
    font-size: 20px;
    padding: 12px 20px;
    border: 1px solid rgb(90, 90, 90);
    background-color: #fff;
    border-radius: 3px;
    width: 111px;
    outline: none;

    transition: all 0.4s ease;
}

.Submit input:nth-child(2):hover {
    background-color: rgb(90, 90, 90);
    color: white;
    cursor: pointer;
}
